<template>
  	<div class="animated slideInLeft" style="animation-duration:100ms">
  		<div id="target" class="col-lg-15 control-section">
  			<div class="content-wrapper">
  				<ejs-toolbar id="toolbargrid2" :clicked="addEditHandler">
  					<e-items>
  						<e-item align="right" id="add" :template="addTemplate" :text="$ml.get('add')"></e-item>
  						<e-item align="right" id="delete" :template="deleteTemplate" :text="$ml.get('delete')"></e-item>
  					</e-items>
  				</ejs-toolbar>
  				<div class="control-section">
  					<ejs-treegrid id="subjectgrid" ref='subjectgrid' :treeColumnIndex='1' childMapping="children" :rowHeight='rowHeight' :allowResizing='true' :enableCollapseAll="false" :allowPdfExport="true" :height="height" :allowReordering="true" :allowExcelExport="true" :dataSource="subjects" :allowFiltering='true' :filterSettings='filterOptions' :allowTextWraping="true" :allowSelection='true' :allowSorting='true' :actionBegin="actionBegin" :toolbarClick="clickHandler" :recordDoubleClick="editSubject" :toolbar="toolbar" :enableHover='false'>
  						<e-columns>
  							<e-column field='_id' :visible="false"></e-column>
  							<e-column field='subject_name' headerText='Name'></e-column>
  							<e-column field='subject_code' headerText='Subject Code'></e-column>
  							<e-column field='subject_credits' headerText='Subject Credits'></e-column>
  							<e-column field='max_marks' headerText='Max. Marks'></e-column>
  						</e-columns>
  					</ejs-treegrid>
  				</div>
  			</div>
  		</div>
  		<b-modal :title="$ml.get('addsubject')" size="sm" class="modal-primary" v-model="addModal" @ok="addModal = false" hide-footer>
  			<b-form v-on:submit.prevent="addSubject">
  				<label v-text="$ml.get('name')"></label>
				<b-form-input type="text" v-model="subject.subject_name" :placeholder="$ml.get('name')"></b-form-input>
  				<label v-text="$ml.get('subjectcode')"></label>
				<b-form-input type="text" v-model="subject.subject_code" :placeholder="$ml.get('subjectcode')"></b-form-input>
  				<label v-text="$ml.get('subjectcredits')"></label>
				<b-form-input type="number" v-model="subject.subject_credits" :placeholder="$ml.get('subjectcredits')"></b-form-input>
  				<label v-text="$ml.get('maxmarks')"></label>
				<b-form-input type="number" v-model="subject.max_marks" :placeholder="$ml.get('maxmarks')"></b-form-input>
  				<label v-text="$ml.get('parentsubject')"></label>
				<treeselect :default-expand-level="10" :placeholder="$ml.get('parentsubject')" v-model="subject.parent_subject" :multiple="false" :options="subjects" />
				<br>
				<b-button type="submit" size="sm" variant="primary" v-text="$ml.get('submit')"><i class="fa fa-dot-circle-o"></i></b-button>
  			</b-form>
	  	</b-modal>
	  	<b-modal :title="$ml.get('editsubject') + ' : ' + editsubject.subject_name" size="sm" class="modal-primary" v-model="editModal" @ok="editModal = false" hide-footer>
	  		<b-form v-on:submit.prevent="sendEditSubject">
  				<label v-text="$ml.get('name')"></label>
				<b-form-input type="text" v-model="editsubject.subject_name" :placeholder="$ml.get('name')"></b-form-input>
  				<label v-text="$ml.get('subjectcode')"></label>
				<b-form-input type="text" v-model="editsubject.subject_code" :placeholder="$ml.get('subjectcode')"></b-form-input>
  				<label v-text="$ml.get('subjectcredits')"></label>
				<b-form-input type="number" v-model="editsubject.subject_credits" :placeholder="$ml.get('subjectcredits')"></b-form-input>
  				<label v-text="$ml.get('maxmarks')"></label>
				<b-form-input type="number" v-model="editsubject.max_marks" :placeholder="$ml.get('maxmarks')"></b-form-input>
  				<label v-text="$ml.get('parentsubject')"></label>
				<treeselect @open="checkParent" :default-expand-level="10" :placeholder="$ml.get('parentsubject')" v-model="editsubject.parent_subject" :multiple="false" :options="subjects" />
				<br>
				<b-button type="submit" size="sm" variant="primary" v-text="$ml.get('submit')"><i class="fa fa-dot-circle-o"></i></b-button>
  			</b-form>
	  	</b-modal>
  	</div>
</template>
	
<script>

	//Imports
	import { acadUrl } from '@/apiUrl'
	import axios from 'axios'
	import Vue from 'vue'
	import VueNotifications from 'vue-notifications'
	import miniToastr from 'mini-toastr'
	import { ToolbarPlugin } from "@syncfusion/ej2-vue-navigations";
	import Treeselect from '@riophae/vue-treeselect'
	import '@riophae/vue-treeselect/dist/vue-treeselect.css'
	import { ExcelExport,PdfExport,TreeGridPlugin, Edit, Filter,CommandColumn, Toolbar, TreeGridComponent, Sort, Reorder,Resize, Page } from "@syncfusion/ej2-vue-treegrid";

	//Declarations
    var api = axios.create({
	  withCredentials :true
	})
	const toastTypes = {
	  success: 'success',
	  error: 'error',
	  info: 'info',
	  warn: 'warn'
	}
	const options = {
	  success: toast,
	  error: toast,
	  info: toast,
	  warn: toast
	}
	function toast ({title, message, type, timeout, cb}) {
	  return miniToastr[type](message, title, timeout, cb)
	}

	//Function Calls
	miniToastr.init({types: toastTypes})
	Vue.use(VueNotifications, options)
	Vue.use(TreeGridPlugin)
	Vue.use(ToolbarPlugin)


    //Vue Handler
	export default {
		name:"SubjectList",
		components:{
			Treeselect
		},
		provide:{
			treegrid: [ ExcelExport,PdfExport,CommandColumn,Edit, Toolbar, Filter, Sort, Reorder, Page, Resize ]
		},
		data : function () {
			return {
				//Grid Configurations
				rowHeight:30,
				height:window.innerHeight - 170,
				filterOptions: {
                    type: 'CheckBox'
                },
                toolbar: [
                	'Search', 'ColumnChooser', { prefixIcon: 'e-small-icon', id: 'big', align: 'Right' }, { prefixIcon: 'e-medium-icon', id: 'medium', align: 'Right' }, { prefixIcon: 'e-big-icon', id: 'small', align: 'Right' }
                ],
                or_subjects:[],
                subjects:[],
                //Toolbar Configurations
                addTemplate: function () {return {template: Vue.component("addTemplate", {template: `<b-badge id="label1" variant="success" ><i class="fa fa-plus"></i>&nbsp<span v-text="$ml.get('add')"></span></b-badge>`, data() {return {data: {}, }; }, }) } },
                deleteTemplate: function () {return {template: Vue.component("deleteTemplate", {template: `<b-badge id="label1" variant="primary" ><i class="fa fa-trash-o"></i>&nbsp<span v-text="$ml.get('delete')"></span></b-badge>`, data() {return {data: {}, }; }, }) } },
                //Modal Options
                addModal:false,
                //Add subject Form Inputs
                subject:{},
                editModal:false,
                editsubject:{}
            }
		},
		methods:{
			//Add Request for subjects 
			addSubject(args) {
				console.log(this.subject)
				api.post(`${acadUrl}sub/subject/create`,this.subject).then((subject) => {
					this.addModal = false
					this.subject = {}
					if(subject.data.lft) {
						api.get(`${acadUrl}sub/subject/view/all`).then((subjects) => {
							this.or_subjects = JSON.parse(JSON.stringify(subjects.data))
							this.subjects = this.list_to_tree_subj(subjects.data)
						})
					}
					else {
						toast({
				          type: VueNotifications.types.error,
				          title: 'Something Went Wrong !!'
				        })
					}
				})
			},
			//Send Edit Subject Request
			sendEditSubject(args) {
				console.log(this.editsubject)
				var id = this.editsubject._id
				api.put(`${acadUrl}sub/subject/update/one/${id}`,this.editsubject).then((updatedsubject) => {
					this.editModal = false
					this.editsubject = {}
					if(updatedsubject.data.message) {
						api.get(`${acadUrl}sub/subject/view/all`).then((subjects) => {
							this.or_subjects = JSON.parse(JSON.stringify(subjects.data))
							this.subjects = this.list_to_tree_subj(subjects.data)
						})
					}
					else {
						toast({
				          type: VueNotifications.types.error,
				          title: 'Something Went Wrong !!'
				        })
					}
				})
			},
			//Convert Flat Data to Tree Data
			list_to_tree_subj(list) {
	          var map = {}, node, roots = [], i;
	          for (i = 0; i < list.length; i += 1) {
	              map[list[i]._id] = i; // initialize the map
	              list[i].children = []; // initialize the children
	          }
	          for (i = 0; i < list.length; i += 1) {
	              node = list[i];
	              if (node.parent_subject != undefined && this.checkExistSubj(node.parent_subject)) {
	                  // if you have dangling branches check that map[node.parentId] exists
	                  list[map[node.parent_subject]].children.push(node);
	              } else {
	                  roots.push(node);
	              }
	          }
	          this.convertDataSubj(roots)
	          return roots
	      },
	      // Check if Parent Subject exists
	      checkExistSubj(node) {
	        for (var i=0; i < this.or_subjects.length; i++) {
	            if (this.or_subjects[i]._id == node)
	                return true;
	        }
	        return false;
	      },
	      // Convert Data for Treeselect
	      convertDataSubj(roots) {
	        for(var i=0;i<roots.length;i++) {
	            if(roots[i].children.length !=0) {
	              this.convertDataSubj(roots[i].children);
	            }
	            roots[i].id = roots[i]._id;
	          roots[i].label = roots[i].subject_name;
	        if(roots[i].children.length<=0) {
	          delete roots[i].children
	        }
	        }
	      },
			// Grid Events
			actionBegin(args) {

			},
			editSubject(args) {
				this.editsubject = args.rowData
				this.editModal = true
				// this.$router.push(`/academics/subject/details/${args.rowData._id}`)
			},
			// Toolbar Handler
			addEditHandler(args) {
				var selected = this.$refs.subjectgrid.getSelectedRecords()
				if(args.item.id=="add") {
					this.addModal = true
					if(selected[0]) {
						this.subject.parent_subject = selected[0]._id
					}
					else{
						this.subject.parent_subject = null
					}
				}
				if(args.item.id=="delete") {
					if(selected.length>0) {
						api.delete(`${acadUrl}sub/subject/delete/one/${selected[0]._id}`).then((deletedsubject) => {
							if(deletedsubject.data.deleted) {
								api.get(`${acadUrl}sub/subject/view/all`).then((subjects) => {
									this.or_subjects = JSON.parse(JSON.stringify(subjects.data))
									this.subjects = this.list_to_tree_subj(subjects.data)
								})
							}
							else {
								toast({
						          type: VueNotifications.types.error,
						          title: 'Something Went Wrong !!'
						        })
							}
						})
					}
				}
			},
			// Check for Same node cannot be own parent
			checkParent(args) {
				api.get(`${acadUrl}sub/subject/view/all`).then((subjects) => {
					for(var i=0;i<subjects.data.length;i++) {
			            if(subjects.data[i]._id == this.editsubject._id) {
			              subjects.data[i].isDisabled = true
			            }
			          }
					this.subjects = this.list_to_tree_subj(subjects.data)
				})
		      },
			// Grid Toolbar Handler 
			clickHandler(args) {
				if (args.item.id === 'small') {
                    this.rowHeight = 20;
                }
                if (args.item.id === 'medium') {
                    this.rowHeight = 40;
                }
                if (args.item.id === 'big') {
                    this.rowHeight = 60;
                }
                if (args.item.text === 'Excel Export') {
                    this.$refs.overviewgrid.excelExport()
                }
                if (args.item.text === 'PDF Export') {
                    this.$refs.overviewgrid.pdfExport()
                }
			},
		},
		mounted(){
			//Get subject List
			api.get(`${acadUrl}sub/subject/view/all`).then((subjects) => {
				this.or_subjects = JSON.parse(JSON.stringify(subjects.data))
				this.subjects = this.list_to_tree_subj(subjects.data)
			})
		},
	};
</script>

<style scoped>
	#subjectgrid {
		min-height: calc( 100vh - 93px );
	}
</style>

